import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import ScheduleReportFormComponent from './ScheduleReportFormComponent';
import { openModal, MODAL_TYPES } from '../../../redux/actions/modalActions';
import { getReportsPayload } from '../../../helpers/reportsPayloadHelper';

const mapStateToProps = state => {
  const { accounts, selectedAccountId } = state.user;
  return {
    accounts,
    selectedAccountId,
    initialValues: {
      type: 'TradeActivity',
      frequency: 'Daily',
      [selectedAccountId]: true
    }
  };
};

const ScheduleReportForm = reduxForm({
  form: 'scheduleReport',
  onSubmit: (values, dispatch, ownProps) => {
    const payload = getReportsPayload(values, ownProps);
    return dispatch(
      openModal(MODAL_TYPES.CONFIRM_CYCLICAL_REPORT, { payload })
    );
  }
})(ScheduleReportFormComponent);

export default connect(mapStateToProps)(ScheduleReportForm);



