import { connect } from 'react-redux';
import { reduxForm, change } from 'redux-form';
import DepositFormComponent from './DepositFormComponent';
import {
  fetchAllProductDepositTemplates,
  createDepositTicket,
  fetchProductDepositTemplate
} from '../../redux/actions/depositActions';
import { openModal, MODAL_TYPES } from '../../redux/actions/modalActions';
import config from '../../config';

const formName = 'createDeposit';

const mapStateToProps = (state, { product }) => {
  return {
    deposit: state.deposit,
    isDasc: product.Product === 'DASC' || product.Product === 'DAS'
  };
};

const mapDispatchToProps = dispatch => ({
  selectDepositProduct: productId =>
    dispatch(fetchAllProductDepositTemplates(productId)),
  selectDepositTemplate: (productId, templateType) =>
    dispatch(fetchProductDepositTemplate(productId, templateType)),
  changeFieldValue: (field, value) => dispatch(change(formName, field, value))
});

const DepositFormContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(DepositFormComponent);

export default reduxForm({
  form: formName,
  onSubmit: (payload, dispatch, { product: { ProductId, Product } }) => {
    dispatch(createDepositTicket({ ...payload, ProductId }));
    if (config.Deposit.showDepositIdModal) {
      dispatch(
        openModal(MODAL_TYPES.DEPOSIT_REFERENCE_ID, {
          ProductSymbol: Product
        })
      );
    }
  }
})(DepositFormContainer);



