import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import RetailFiatWithdrawFormComponent from './RetailFiatWithdrawFormComponent';
import { closeSidePane } from '../../../../redux/actions/sidePaneActions';
import {
  fetchProductWithdrawTemplates,
  fetchProductWithdrawTemplate,
  createWithdrawTicket
} from '../../../../redux/actions/withdrawActions';
import { openModal, MODAL_TYPES } from '../../../../redux/actions/modalActions';
import { kycVerificationLevelsSelector } from '../../../../redux/selectors/kycLevelsSelectors';

const mapStateToProps = state => {
  const { disableWithdraw } = kycVerificationLevelsSelector(state);
  return {
    disableWithdraw,
    withdrawInfo: state.withdraw,
    products: state.product
  };
};

const mapDispatchToProps = dispatch => ({
  verificationOnClick: () => dispatch(closeSidePane()),
  confirmWithdraw: payload => dispatch(createWithdrawTicket(payload)),
  selectWithdrawProduct: productId =>
    dispatch(fetchProductWithdrawTemplates(productId)),
  selectWithdrawTemplate: (productId, templateType) =>
    dispatch(fetchProductWithdrawTemplate(productId, templateType))
});

const RetailFiatWithdrawForm = reduxForm({
  form: 'createFiatWithdraw',
  onSubmit: (payload, dispatch, { fee, withdrawInfo: { template } }) => {
    return dispatch(
      openModal(MODAL_TYPES.CONFIRM_FIAT_WITHDRAW, { payload, fee, template })
    );
  }
})(RetailFiatWithdrawFormComponent);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailFiatWithdrawForm);



