import React from 'react';
import PropTypes from 'prop-types';
import sha256 from 'crypto-js/hmac-sha256';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './ConfirmAPIKeyModal.css';

const baseClasses = getBEMClasses('confirm-api-key');

const ConfirmAPIKeyModal = (props, context) => {
  const { Permissions } = props.item;
  const nonce = window.crypto.getRandomValues(new Uint32Array(1)).toString();
  const {
    userId,
    item: { APIKey, APISecret }
  } = props;
  return (
    <div className={baseClasses()}>
      <div className={baseClasses('key-info-container')}>
        <APLabelWithText
          label={context.t('Permissions')}
          text={Permissions.join(', ')}
          customClass={baseClasses()}
        />
        <APLabelWithText
          label={context.t('Key')}
          text={APIKey}
          customClass={baseClasses()}
        />
        {APISecret && (
          <React.Fragment>
            <APLabelWithText
              label={context.t('Secret')}
              text={APISecret}
              customClass={baseClasses()}
            />
          </React.Fragment>
        )}
      </div>
      {APISecret && (
        <React.Fragment>
          <div className={baseClasses('authenticate-user')}>
            <p>
              {context.t('Call {a} with the following to login:', {
                a: <code>AuthenticateUser</code>
              })}
            </p>
            <pre>
              {JSON.stringify(
                {
                  APIKey,
                  Signature: sha256(
                    `${nonce}${userId}${APIKey}`,
                    APISecret
                  ).toString(),
                  UserId: userId.toString(),
                  Nonce: nonce
                },
                null,
                2
              )}
            </pre>
            <p>
              {context.t(
                'To generate your own signature with a different nonce, {hmacSha256} encode your nonce, user ID, and API key, in the format {messageFormat} using the secret as your key.',
                {
                  hmacSha256: (
                    <a
                      href="https://codebeautify.org/hmac-generator"
                      target="_blank"
                      rel="noopener noreferrer">
                      HMAC-Sha256
                    </a>
                  ),
                  messageFormat: <code>NonceUserIdAPIKey</code>
                }
              )}
            </p>
          </div>
        </React.Fragment>
      )}
    </div>
  );
};

ConfirmAPIKeyModal.contextTypes = {
  t: PropTypes.func.isRequired
};

ConfirmAPIKeyModal.propTypes = {
  item: PropTypes.object,
  index: PropTypes.number,
  onRemove: PropTypes.func
};

ConfirmAPIKeyModal.defaultProps = {
  index: 0
};

export default ConfirmAPIKeyModal;



