import { connect } from 'react-redux';
import { get } from 'lodash';
import RetailFiatSidePaneComponent from './RetailFiatSidePaneComponent';
import {
  createWithdrawTicket,
  getWithdrawFee
} from '../../../redux/actions/withdrawActions';

import { positionByProductIdSelector } from '../../../redux/selectors/retailFiatSidePaneSelectors';
import config from '../../../config';

const maptStateToProps = (state, ownProps) => {
  const productSymbol = get(ownProps, 'product.ProductSymbol', null);
  const productConfig = config.Deposit[productSymbol] || {};
  const productId = get(ownProps, 'product.ProductId', null);
  const position = positionByProductIdSelector(productId)(state);
  const {
    withdrawFee: fee,
    template: { TemplateType }
  } = state.withdraw;
  const withdrawDetailsInfo = config.Withdraw.WithdrawDetails;

  return { position, fee, TemplateType, productConfig, withdrawDetailsInfo };
};

const mapDispatchToProps = dispatch => ({
  sendToAddress: payload => dispatch(createWithdrawTicket(payload)),
  getWithdrawFee: (productId, amount) =>
    dispatch(getWithdrawFee(productId, amount))
});

export default connect(
  maptStateToProps,
  mapDispatchToProps
)(RetailFiatSidePaneComponent);



