import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../../common/Modal/Modal';
import ConfirmCyclicalReportForm from './ConfirmCyclicalReportForm';

const ConfirmCyclicalReportModal = (props, context) => {
  return (
    <Modal
      isOpen={props.isOpen}
      title={context.t('Schedule a Cyclical Report')}
      customClass="confirm-report-modal"
      close={props.close}
      footer={{
        buttonText: context.t('Schedule Report'),
        buttonStyle: 'additive',
        onClick: () => props.scheduleReport(props.payload)
      }}>
      <ConfirmCyclicalReportForm payload={props.payload} />
    </Modal>
  );
};

ConfirmCyclicalReportModal.propTypes = {
  payload: PropTypes.object,
  close: PropTypes.func.isRequired
};

ConfirmCyclicalReportModal.defaultProps = {
  payload: {
    type: '',
    startDate: '',
    frequency: ''
  }
};

ConfirmCyclicalReportModal.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ConfirmCyclicalReportModal;



