import React from 'react';
import PropTypes from 'prop-types';
import addDays from 'date-fns/add_days';
import subDays from 'date-fns/sub_days';
import APSelect from '../../common/APSelect/APSelect';
import APButton from '../../common/APButton';
import APDatePicker from '../../common/APDatePicker/APDatePicker';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import { required } from '../../../helpers/formValidations';
import ReportAccountsComponent from '../ReportAccountsComponent';
import '../ReportFormComponent.css';

const baseClasses = getBEMClasses('report-form');

const CreateReportComponent = (props, context) => {
  const { accounts, startDate, invalid } = props;
  return (
    <form onSubmit={props.handleSubmit} className={baseClasses('body')}>
      <div className={baseClasses('form-body')}>
        <ReportAccountsComponent accounts={accounts} />
        <APSelect
          name="type"
          label={context.t('Report Type:')}
          customClass={baseClasses()}
          validate={[required]}
          options={[
            { value: 'TradeActivity', label: context.t('Trade Activity') },
            { value: 'Transaction', label: context.t('Transaction') },
            { value: 'Treasury', label: context.t('Treasury') }
          ]}
          showTriangles={true}
        />
        <div className={baseClasses('datapicker-container')}>
          <APDatePicker
            name="startDate"
            label={context.t('Start Date:')}
            customClass={baseClasses()}
            validate={[required]}
            maxDate={subDays(new Date(), 1)}
            initialOptions={{ theme: 'retail' }}
          />

          <APDatePicker
            name="endDate"
            label={context.t('End Date:')}
            customClass={baseClasses()}
            minDate={addDays(startDate, 1)}
            maxDate={new Date()}
            validate={[required]}
            initialOptions={{ theme: 'retail' }}
          />
        </div>
      </div>
      <div className={baseClasses('footer')}>
        <APButton
          type="submit"
          customClass={baseClasses()}
          styleName="additive"
          disabled={invalid}>
          {context.t('Create')}
        </APButton>
      </div>
    </form>
  );
};

CreateReportComponent.propTypes = {
  handleSubmit: PropTypes.func.isRequired
};

CreateReportComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default CreateReportComponent;



