import React from 'react';
import PropTypes from 'prop-types';
import APCheckbox from '../common/APCheckbox/APCheckbox';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import './ReportFormComponent.css';

const baseClasses = getBEMClasses('report-form');

class ReportAccountsComponent extends React.Component {
  renderCheckbox = account => {
    const id = account.AccountId.toString();
    return (
      <APCheckbox
        name={id}
        id={id}
        key={id}
        label={account.AccountName}
        customClass={baseClasses()}
      />
    );
  };

  render() {
    const { accounts } = this.props;
    return (
      <div className={baseClasses('checkbox-wrapper')}>
        <label className={`ap--label ${baseClasses('checkbox-label')}`}>
          {this.context.t('Accounts')}
        </label>
        {accounts && accounts.map(this.renderCheckbox)}
      </div>
    );
  }
}
ReportAccountsComponent.propTypes = {
  accounts: PropTypes.array.isRequired
};

ReportAccountsComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ReportAccountsComponent;



