import React from 'react';
import PropTypes from 'prop-types';
import APCheckbox from '../../common/APCheckbox/APCheckbox';
import APButton from '../../common/APButton';
import classnames from 'classnames';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './CreateApiKeyComponent.css';

const baseClasses = getBEMClasses('create-api-key');

const CreateApiKeyComponent = (props, context) => {
  const { error, pristine } = props;

  return (
    <form
      onSubmit={props.handleSubmit}
      className={classnames('form-inline', baseClasses('slide-pane-body'))}>
      <div className={baseClasses('header')}>
        {context.t('Create New API Key')}
      </div>
      <div className={baseClasses('form-body')}>
        <div className={baseClasses('frame')}>
          <div className={baseClasses('title')}>
            {context.t(
              'Choose the following activities to allow the API Key to access:'
            )}
          </div>

          <div className={baseClasses('input-wrapper')}>
            <APCheckbox
              name="trading"
              label={context.t('Trading')}
              id="trading"
              customClass={baseClasses()}
            />
          </div>

          <div className={baseClasses('input-wrapper')}>
            <APCheckbox
              name="deposit"
              label={context.t('Deposits')}
              id="deposit"
              customClass={baseClasses()}
            />
          </div>

          <div className={baseClasses('input-wrapper')}>
            <APCheckbox
              name="withdraw"
              label={context.t('Withdrawals')}
              id="withdraw"
              customClass={baseClasses()}
            />
          </div>
          {error && pristine ? (
            <div className={baseClasses('error')}>{error}</div>
          ) : null}
        </div>

        <div className={baseClasses('footer')}>
          <APButton
            type="submit"
            customClass={baseClasses()}
            disabled={props.submitting}
            styleName="additive">
            {props.submitting
              ? context.t('Processing...')
              : context.t('Create')}
          </APButton>
        </div>
      </div>
    </form>
  );
};

CreateApiKeyComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

CreateApiKeyComponent.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  submitting: PropTypes.bool.isRequired
};

export default CreateApiKeyComponent;



