import React from 'react';
import PropTypes from 'prop-types';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import { formatDate } from '../../../helpers/dateHelper';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './ConfirmSingleReportModal.css';

const confirmSingleReportClasses = getBEMClasses('confirm-single-report');

const ConfirmSingleReportForm = (props, context) => {
  return (
    <div className={confirmSingleReportClasses()}>
      <p className={confirmSingleReportClasses('title')}>
        {context.t('Confirm')}
      </p>
      <APLabelWithText
        label={context.t('Report Type:')}
        text={props.payload.type}
        customClass={confirmSingleReportClasses()}
      />
      <APLabelWithText
        label={context.t('Start Date:')}
        text={formatDate(props.payload.startDate)}
        customClass={confirmSingleReportClasses()}
      />
      <APLabelWithText
        label={context.t('End Date:')}
        text={formatDate(props.payload.endDate)}
        customClass={confirmSingleReportClasses()}
      />
    </div>
  );
};

ConfirmSingleReportForm.propTypes = {
  payload: PropTypes.object
};

ConfirmSingleReportForm.defaultProps = {
  payload: {
    type: '',
    startDate: '',
    endDate: ''
  }
};

ConfirmSingleReportForm.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ConfirmSingleReportForm;



