import { connect } from 'react-redux';
import RetailFiatWithdrawModal from './RetailFiatWithdrawModal';
import modalDecorator from '../../../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../../../redux/actions/modalActions';
import { createWithdrawTicket } from '../../../../redux/actions/withdrawActions';

const mapStateToProps = state => {
  return {
    products: state.product
  };
};

const mapDispatchToProps = {
  submitWithdraw: createWithdrawTicket
};

const container = connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailFiatWithdrawModal);

export default modalDecorator({
  name: MODAL_TYPES.CONFIRM_FIAT_WITHDRAW
})(container);



