import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../common/APButton';
import APNumberInput from '../common/APNumberInput';
import APSelect from '../common/APSelect/APSelect';
import APLabelWithText from '../common/APLabelWithText/APLabelWithText';
import APCheckbox from '../common/APCheckbox/APCheckbox';
import { instrumentPropType } from '../../propTypes/instrumentPropTypes';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { styleNames } from '../../propTypes/commonComponent';
import {
  buyValue,
  sellValue
} from '../../constants/sendOrder/orderEntryFormConstants';
import {
  getOrderNet,
  getOrderFee,
  getOrderTotal
} from '../../helpers/orderHelper';
import UserBalanceComponent from '../UserBalance/UserBalanceComponent';
import APInput from '../common/APInput/APInput';
import { biggerThanZero } from '../../helpers/formValidations';

import './ReportBlockTradeForm.css';

const bemClasses = getBEMClasses(['report-block-trade-form']);
const slidePaneClasses = getBEMClasses('slide-pane');
const dividerClasses = getBEMClasses('divider');

const ReportBlockTradeForm = (props, context) => {
  const {
    position1,
    position2,
    instruments,
    handleSubmit,
    submitting,
    disableTrading,
    formObj,
    decimalPlaces,
    selectedInstrument,
    invalid,
    positions,
    VerificationRequiredComponent,
    showLockedIn
  } = props;

  const { side } = formObj.values;
  const boughtProductSymbol =
    side === buyValue
      ? selectedInstrument.Product1Symbol
      : selectedInstrument.Product2Symbol;
  const soldProductSymbol =
    side === buyValue
      ? selectedInstrument.Product2Symbol
      : selectedInstrument.Product1Symbol;

  return (
    <form className={slidePaneClasses('body')} onSubmit={handleSubmit}>
      <div className={bemClasses('main-section')}>
        <div>
          <div className={bemClasses(null, ['with-padding'])}>
            <APSelect
              name="instrument"
              label={`${context.t('Instrument')}:`}
              customClass={bemClasses()}
              options={instruments.map(instrument => {
                const { InstrumentId } = instrument;
                return { value: InstrumentId, label: instrument.Symbol };
              })}
            />
          </div>

          <div className={bemClasses('counterparty')}>
            <APInput
              name="counterparty"
              label={`${context.t('Counterparty')}:`}
              customClass={bemClasses()}
              validate={[biggerThanZero]}
            />
            {showLockedIn && (
              <div className={bemClasses('checkbox-container')}>
                <APCheckbox
                  name="lockedIn"
                  type="checkbox"
                  id="locked-in"
                  label={context.t('Locked In')}
                />
              </div>
            )}
          </div>
        </div>
      </div>

      <div className={dividerClasses(null, ['dark', 'side-pane-size'])} />

      <div>
        <div className={bemClasses(null, ['with-padding'])}>
          <APNumberInput
            name="quantity"
            placeholder="0"
            label={`${context.t('Product Bought')}:`}
            decimalPlaces={decimalPlaces[boughtProductSymbol]}
            customClass={bemClasses()}
            labelInInput={boughtProductSymbol}
            validate={[biggerThanZero]}
          />
        </div>

        <APNumberInput
          name="price"
          placeholder="0"
          label={`${context.t('Product Sold')}:`}
          decimalPlaces={decimalPlaces[soldProductSymbol]}
          customClass={bemClasses()}
          labelInInput={soldProductSymbol}
          validate={[biggerThanZero]}
        />
      </div>

      <div className={dividerClasses(null, ['dark', 'side-pane-size'])} />

      <div className={bemClasses(null, 'with-small-padding')}>
        <APLabelWithText
          label={`${context.t('Fees')}:`}
          text={getOrderFee(formObj, positions)}
          customClass={bemClasses()}
        />
      </div>

      <div className={bemClasses(null, 'with-small-padding')}>
        <APLabelWithText
          label={`${context.t('Received')}:`}
          text={getOrderNet(formObj, selectedInstrument)}
          customClass={bemClasses()}
        />
      </div>

      <APLabelWithText
        label={`${context.t('Order Total')}:`}
        text={getOrderTotal(formObj, selectedInstrument)}
        customClass={bemClasses()}
      />

      <div className={dividerClasses(null, ['dark', 'side-pane-size'])} />

      <div className={bemClasses('user-balance-container')}>
        <UserBalanceComponent position1={position1} position2={position2} />
      </div>

      <div className={dividerClasses(null, ['dark', 'side-pane-size'])} />

      <APButton
        type="submit"
        disabled={submitting || invalid || disableTrading}
        customClass={bemClasses()}
        styleName={
          formObj.values.side === sellValue
            ? styleNames.subtractive
            : styleNames.additive
        }>
        {submitting ? context.t('Processing...') : context.t('Submit Report')}
      </APButton>

      <div className={bemClasses('verification-required')}>
        {VerificationRequiredComponent}
      </div>
    </form>
  );
};

ReportBlockTradeForm.defaultProps = {
  showLockedIn: true
};

ReportBlockTradeForm.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  position1: PropTypes.object.isRequired,
  position2: PropTypes.object.isRequired,
  instruments: PropTypes.arrayOf(instrumentPropType).isRequired,
  formObj: PropTypes.object,
  submitting: PropTypes.bool.isRequired,
  VerificationRequiredComponent: PropTypes.element,
  disableTrading: PropTypes.bool.isRequired,
  showLockedIn: PropTypes.bool.isRequired
};

ReportBlockTradeForm.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ReportBlockTradeForm;



