import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import APSelect from '../../common/APSelect/APSelect';
import WithdrawTemplateFormContainer from '../WithdrawTemplateForm/WithdrawTemplateFormContainer';
import Spinner from '../../common/Spinner/Spinner';
import { formatNumberToLocale } from '../../../helpers/numberFormatter';
import {
  getTotalRender,
  getTotalWithdrawValue,
  getAvailableBalance
} from '../../../helpers/withdrawHelper';

import './WithdrawFormComponent.css';

const baseClasses = getBEMClasses('withdraw-form');
const slidePaneClasses = getBEMClasses('slide-pane');

class WithdrawFormComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = this.getInitialState();
  }

  componentWillMount() {
    const { product } = this.props;
    this.selectProduct(product.ProductId);
  }

  getInitialState = () => ({ position: null, product: {}, errorMessage: '' });

  selectProduct = productId => {
    const { selectWithdrawProduct, products } = this.props;
    const product = products.find(product => product.ProductId === productId);

    if (product) {
      const position = this.getPosition(product.Product);

      selectWithdrawProduct(product.ProductId);
      this.setState({ position, product });
    } else {
      this.setState(this.getInitialState());
    }
  };

  getPosition = productId =>
    this.props.positions.find(position => position.ProductSymbol === productId);

  onAmountChange = amount => {
    const { product } = this.state;
    if (!isNaN(amount)) this.props.getWithdrawFee(product.ProductId, amount);
  };

  doSubmit = values => {
    const { Amount, Hold } = this.state.position;
    const {
      withdrawInfo: {
        withdrawFee: fee,
        template: { TemplateType }
      },
      submitWithdraw
    } = this.props;
    const amount = parseFloat(values.Amount);
    const balance = getAvailableBalance(Amount, Hold);

    if (amount <= balance) {
      values.Amount = getTotalWithdrawValue(balance, values.Amount, fee);
    } else {
      this.setState({
        errorMessage: this.context.t('You have insufficient balance')
      });
      return;
    }
    return submitWithdraw({
      ...values,
      TemplateType,
      ProductId: this.state.product.ProductId
    });
  };

  getTotal = (balance, amount, fee) => {
    const total = getTotalRender(balance, amount, fee);

    return total || total === 0 ? total : this.context.t('Insufficient funds');
  };

  getInfoText = value => {
    const { product } = this.state;
    return (
      <React.Fragment>
        <span className={baseClasses('currency')}>
          {formatNumberToLocale(value, product && product.Product)}
        </span>{' '}
        {product && product.Product}
      </React.Fragment>
    );
  };

  render() {
    const {
      product,
      position: { Amount, Hold },
      errorMessage
    } = this.state;
    const {
      handleSubmit,
      submitting,
      withdrawInfo,
      amountValue,
      invalid,
      selectWithdrawTemplate,
      hideFees
    } = this.props;
    const balance = getAvailableBalance(Amount, Hold);
    const total = this.getTotal(balance, amountValue, withdrawInfo.withdrawFee);
    const balanceText = this.getInfoText(balance);
    const feeText = this.getInfoText(withdrawInfo.withdrawFee);
    const totalText = this.getInfoText(total);
    const receivedText = getTotalWithdrawValue(
      balance,
      amountValue,
      withdrawInfo.withdrawFee
    );
    const showTemplateTypeSelect =
      withdrawInfo.templateTypes.length > 1 &&
      !Object.keys(withdrawInfo.templateInfo).length &&
      !withdrawInfo.isLoading;
    const templateTypes = [
      { value: '', label: 'Select an option' },
      ...withdrawInfo.templateTypes.map(template => ({
        value: template.TemplateName,
        label: template.TemplateName
      }))
    ];

    return (
      <form
        onSubmit={handleSubmit(this.doSubmit)}
        className={slidePaneClasses('body')}>
        {withdrawInfo.withdrawStatus.success ? (
          <div className={baseClasses('success')}>
            {this.context.t('Your withdraw ticket was created successfully.')}
          </div>
        ) : (
          <div className={baseClasses('form-body')}>
            {withdrawInfo.error && (
              <div className={baseClasses('error-text')}>
                {this.context.t(withdrawInfo.error)}
              </div>
            )}

            {withdrawInfo.isLoading && <Spinner customClass={baseClasses} />}

            {showTemplateTypeSelect && (
              <React.Fragment>
                <p>
                  {this.context.t(
                    'Select an option to continue the withdraw process'
                  )}
                </p>
                <APSelect
                  name="TemplateType"
                  customClass={baseClasses()}
                  label={this.context.t('Options for withdraw')}
                  onSelect={value =>
                    selectWithdrawTemplate(product.ProductId, value)
                  }
                  options={templateTypes}
                />
              </React.Fragment>
            )}

            <WithdrawTemplateFormContainer
              submitting={submitting}
              invalid={invalid}
              baseClasses={baseClasses}
              balance={balance}
              onAmountChange={this.onAmountChange}
              errorMessage={errorMessage}>
              <div className={baseClasses('section')}>
                {product && (
                  <React.Fragment>
                    <APLabelWithText
                      label={this.context.t('Balance:')}
                      text={balanceText}
                      dataTest="Balance:"
                      customClass={baseClasses()}
                    />

                    {!hideFees && (
                      <APLabelWithText
                        label={this.context.t('Fee:')}
                        text={feeText}
                        dataTest="Fee:"
                        customClass={baseClasses()}
                      />
                    )}

                    <APLabelWithText
                      label={this.context.t('Total:')}
                      text={totalText}
                      dataTest="Total:"
                      customClass={baseClasses()}
                    />

                    <APLabelWithText
                      label={this.context.t('Received:')}
                      text={receivedText}
                      dataTest="Received:"
                      customClass={baseClasses()}
                    />
                  </React.Fragment>
                )}
              </div>
            </WithdrawTemplateFormContainer>
          </div>
        )}
      </form>
    );
  }
}

WithdrawFormComponent.defaultProps = {
  withdrawInfo: {
    templateInfo: {},
    template: {},
    isLoading: false,
    error: '',
    TemplateForm: {},
    withdrawStatus: {},
    getWithdrawFee: () => {}
  }
};

WithdrawFormComponent.propTypes = {
  handleSubmit: PropTypes.func,
  getWithdrawFee: PropTypes.func,
  submitting: PropTypes.bool,
  selectWithdrawProduct: PropTypes.func.isRequired,
  products: PropTypes.arrayOf(
    PropTypes.shape({
      Product: PropTypes.string,
      ProductId: PropTypes.number
    })
  ).isRequired,
  withdrawInfo: PropTypes.shape({
    templateInfo: PropTypes.object.isRequired,
    template: PropTypes.object.isRequired,
    isLoading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    TemplateForm: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
    withdrawStatus: PropTypes.shape({
      success: PropTypes.bool,
      RequestCode: PropTypes.string
    }).isRequired
  }),
  VerificationRequiredComponent: PropTypes.element
};

WithdrawFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default WithdrawFormComponent;



