import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import ProductIconContainer from '../../common/ProductIcon/ProductIconContainer';
import './RetailFiatSidePaneHeader.css';

const bemClasses = getBEMClasses('retail-sidepane-header');

class RetailFiatSidePaneHeader extends React.Component {
  render() {
    const {
      product: { iconFileName, ProductFullName, ProductSymbol }
    } = this.props;
    return (
      <header className={bemClasses()}>
        <ProductIconContainer
          iconFileName={iconFileName}
          customClass={bemClasses('logo')}
        />
        <div className={bemClasses('title')}>
          {ProductSymbol} {ProductFullName}
        </div>
      </header>
    );
  }
}

RetailFiatSidePaneHeader.propTypes = {
  name: PropTypes.string
};

export default RetailFiatSidePaneHeader;



