import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import DepositTemplateFormComponent from './DepositTemplateFormComponent';
import DepositWorkflowsContainer from './DepositWorkflows/DepositWorkflowsContainer';
import Spinner from '../common/Spinner/Spinner';
import APSelect from '../common/APSelect/APSelect';

import './DepositFormComponent.css';

const baseClasses = getBEMClasses('deposit-form');
const slidePaneClasses = getBEMClasses('slide-pane');

class DepositFormComponent extends React.Component {
  componentWillMount() {
    const { selectDepositProduct, product } = this.props;
    selectDepositProduct(product.ProductId);
  }

  render() {
    const {
      handleSubmit,
      submitting,
      deposit,
      product,
      changeFieldValue,
      selectDepositTemplate,
      isDasc
    } = this.props;
    const showTemplateTypeSelect =
      deposit.templateTypes.length > 1 &&
      !Object.keys(deposit.templateInfo).length &&
      !deposit.isLoading;
    const templateTypes = [
      { value: '', label: 'Select an option' },
      ...deposit.templateTypes.map(template => ({
        value: template.TemplateName,
        label: template.TemplateName
      }))
    ];

    if (deposit.depositInfo.length && isDasc) {
      const dataArr = deposit.depositInfo[0].split('?dt=');
      deposit.template = { exchangeAddress: dataArr[0], memo: dataArr[1] };

      setTimeout(() => {
        //update data outside render
        changeFieldValue('exchangeAddress', deposit.template.exchangeAddress);
        changeFieldValue('memo', deposit.template.memo);
      });
    }

    return (
      <form className={slidePaneClasses('body')} onSubmit={handleSubmit}>
        <div className={baseClasses('form-body')}>
          {deposit.error && (
            <p className={baseClasses('error')}>
              {this.context.t(deposit.error)}
            </p>
          )}

          {deposit.isLoading && <Spinner customClass={baseClasses} />}

          {showTemplateTypeSelect && (
            <React.Fragment>
              <p>
                {this.context.t(
                  'Select an option to continue the deposit process'
                )}
              </p>
              <APSelect
                name="TemplateType"
                customClass={baseClasses()}
                label={this.context.t('Options for deposit')}
                onSelect={value =>
                  selectDepositTemplate(product.ProductId, value)
                }
                options={templateTypes}
              />
            </React.Fragment>
          )}

          {!deposit.depositInfo.length && (
            <DepositTemplateFormComponent
              product={product}
              template={deposit.template}
              submitting={submitting}
              baseClasses={baseClasses}
            />
          )}

          <DepositWorkflowsContainer
            submitting={submitting}
            onSubmit={handleSubmit}
          />
        </div>
      </form>
    );
  }
}

DepositFormComponent.defaultProps = {
  deposit: {
    templateInfo: {},
    template: {},
    isLoading: false,
    error: '',
    depositInfo: {},
    depositStatus: {}
  }
};

DepositFormComponent.propTypes = {
  handleSubmit: PropTypes.func,
  submitting: PropTypes.bool,
  selectDepositProduct: PropTypes.func.isRequired,
  showSnack: PropTypes.func,
  deposit: PropTypes.shape({
    templateInfo: PropTypes.object.isRequired,
    template: PropTypes.object.isRequired,
    isLoading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    product: PropTypes.number,
    depositInfo: PropTypes.oneOfType([PropTypes.array, PropTypes.object])
      .isRequired,
    depositStatus: PropTypes.shape({
      success: PropTypes.bool,
      RequestCode: PropTypes.string
    }).isRequired
  })
};

DepositFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default DepositFormComponent;



