import React from 'react';
import PropTypes from 'prop-types';

import CreateApiKeyContainer from '../CreateApiKey/CreateApiKeyContainer';
import NewApiKeyModalContainer from '../NewApiKey/NewApiKeyModalContainer';

export default class ApiKeysSidePaneComponent extends React.Component {
  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  static propTypes = {
    closeSidePane: PropTypes.func.isRequired
  };

  componentDidMount() {
    this.props.onSidePaneOpen({
      hideHeader: true,
      customClass: 'retail-api-keys-sidepane'
    });
  }

  render() {
    return (
      <React.Fragment>
        <CreateApiKeyContainer />
        <NewApiKeyModalContainer />
      </React.Fragment>
    );
  }
}



