import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../../common/Modal/Modal';
import ConfirmAPIKeyModal from '../ConfirmApiKeyModal/ConfirmAPIKeyModal';

const NewApiKeyModal = (props, context) => {
  return (
    <Modal
      isOpen={props.isOpen}
      title={context.t('Create New API Key')}
      customClass="retail-api-keys-modal"
      onCancel={() => {
        props.removeApiKey(props.apiKey.APIKey);
        props.close();
      }}
      close={props.close}
      footer={{
        buttonText: context.t('Confirm'),
        buttonStyle: 'additive',
        onClick: () => props.addApiKey(props.apiKey)
      }}>
      <ConfirmAPIKeyModal
        item={props.apiKey}
        userId={props.userId}
        index={props.numberOfApiKeys}
      />
    </Modal>
  );
};

NewApiKeyModal.propTypes = {
  apiKey: PropTypes.object,
  userId: PropTypes.number.isRequired,
  numberOfApiKeys: PropTypes.number.isRequired,
  addApiKey: PropTypes.func.isRequired,
  removeApiKey: PropTypes.func.isRequired,
  close: PropTypes.func.isRequired
};

NewApiKeyModal.contextTypes = {
  t: PropTypes.func.isRequired
};

export default NewApiKeyModal;



