import { createSelector } from 'reselect';

export const positionSelector = createSelector(
  [state => state.user, state => state.position],
  ({ selectedAccountId }, { positions }) => {
    return Object.values(positions).filter(
      position => position.AccountId === selectedAccountId
    );
  }
);



