import { connect } from 'react-redux';
import ConfirmSingleReportModal from './ConfirmSingleReportModal';
import modalDecorator from '../../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../../redux/actions/modalActions';
import { generateReport } from '../../../redux/actions/reportActions';

const mapDispatchToProps = {
  generateReport
};

const container = connect(
  null,
  mapDispatchToProps
)(ConfirmSingleReportModal);

export default modalDecorator({
  name: MODAL_TYPES.CONFIRM_SINGLE_REPORT
})(container);



