import React from 'react';
import PropTypes from 'prop-types';
import { templateFormRenderer } from '../../../helpers/formTemplateHelper';
import { required, biggerThanZero } from '../../../helpers/formValidations';

import WithdrawWorkflowsContainer from '../WithdrawWorkflows/WithdrawWorkflowsContainer';
import APButton from '../../common/APButton';
import APNumberInput from '../../common/APNumberInput';

class WithdrawTemplateFormComponent extends React.Component {
  onMaxClick = () => {
    const { onAmountChange, change, balance } = this.props;
    change('createWithdraw', 'Amount', balance);
    onAmountChange(balance);
  };

  render() {
    const {
      product,
      product: { Product, ProductId, DecimalPlaces },
      withdrawWorkflowInfo,
      submitting,
      invalid,
      baseClasses,
      onAmountChange,
      children,
      errorMessage,
      disableWithdraw,
      VerificationRequiredComponent
    } = this.props;
    const useExternalWorkflow =
      withdrawWorkflowInfo && Object.values(withdrawWorkflowInfo).length;

    let template = { ...this.props.template };

    if ('ExternalAddress' in template) {
      // Has to be a new object adding/modifying a prop causes infinite renders AW-515
      template = { ExternalAddress: Product, ...this.props.template };
    }

    return Object.keys(template).length && ProductId ? (
      <React.Fragment>
        <div className={baseClasses('wrapper')}>
          {!template.hasOwnProperty('Amount') && (
            <React.Fragment>
              <APNumberInput
                type="text"
                key="Amount"
                name="Amount"
                data-test="Amount"
                label={this.context.t('Amount')}
                customClass={baseClasses()}
                decimalPlaces={DecimalPlaces}
                customChange={value => onAmountChange(+value)}
                validate={[required, biggerThanZero]}
              />
              <APButton
                customClass={baseClasses('max')}
                type="button"
                styleName="additive"
                onClick={this.onMaxClick}>
                {this.context.t('Max')}
              </APButton>
            </React.Fragment>
          )}
          {errorMessage && (
            <div className={baseClasses('error-message')}>{errorMessage}</div>
          )}
          {!useExternalWorkflow &&
            templateFormRenderer(
              template,
              baseClasses(''),
              this.context,
              product
            )}
        </div>
        <WithdrawWorkflowsContainer />
        {!useExternalWorkflow && (
          <React.Fragment>
            {children}
            <div className={baseClasses('section')}>
              <APButton
                customClass={baseClasses()}
                type="submit"
                disabled={submitting || invalid || disableWithdraw}
                styleName="subtractive">
                {submitting
                  ? this.context.t('Submitting...')
                  : this.context.t('Place Withdraw Ticket')}
              </APButton>

              <div className={baseClasses('verification-required-text')}>
                {VerificationRequiredComponent}
              </div>
            </div>
          </React.Fragment>
        )}
      </React.Fragment>
    ) : (
      <div />
    );
  }
}

WithdrawTemplateFormComponent.defaultProps = {
  template: {},
  product: {},
  submitting: false,
  baseClasses: () => {},
  onAmountChange: () => {},
  VerificationRequiredComponent: null
};

WithdrawTemplateFormComponent.propTypes = {
  template: PropTypes.object,
  product: PropTypes.shape({
    Product: PropTypes.string,
    ProductId: PropTypes.number,
    DecimalPlaces: PropTypes.number
  }),
  submitting: PropTypes.bool,
  baseClasses: PropTypes.func,
  onAmountChange: PropTypes.func,
  VerificationRequiredComponent: PropTypes.element
};

WithdrawTemplateFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default WithdrawTemplateFormComponent;



