import React from 'react';
import PropTypes from 'prop-types';
import APLabelWithText from '../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './UserBalanceComponent.css';

const bemClasses = getBEMClasses(['user-balance']);

const UserBalanceComponent = (props, context) => {
  return (
    <div>
      <div className={bemClasses(null, 'with-small-padding')}>
        <APLabelWithText
          label={`${props.position1.ProductSymbol} ${context.t('Balance')}:`}
          text={props.position1.Amount}
          customClass={bemClasses()}
        />
      </div>

      <APLabelWithText
        label={`${props.position2.ProductSymbol} ${context.t('Balance')}:`}
        text={props.position2.Amount}
        customClass={bemClasses()}
      />
    </div>
  );
};

UserBalanceComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default UserBalanceComponent;



