import { connect } from 'react-redux';
import { change } from 'redux-form';
import WithdrawTemplateFormComponent from './WithdrawTemplateFormComponent';
import { withdrawProductSelector } from '../../../redux/selectors/depositWithdrawalSelectors';

const mapStateToProps = (
  state,
  { submitting, invalid, onAmountChange, baseClasses, errorMessage }
) => ({
  template: state.withdraw.template,
  withdrawWorkflowInfo: state.withdraw.withdrawWorkflowInfo,
  product: withdrawProductSelector(state),
  onAmountChange,
  submitting,
  invalid,
  baseClasses,
  errorMessage
});

const mapDispatchToProps = { change };

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WithdrawTemplateFormComponent);



