import React from 'react';
import PropTypes from 'prop-types';
import Tab from '../common/Tab/Tab';
import CreateReportFormContainer from './CreateReportForm/CreateReportFormContainer';
import ScheduleReportComponent from './ScheduleReportComponent';
import ConfirmSingleReportModalContainer from './ConfirmSingleReport/ConfirmSingleReportModalContainer';
import ConfirmCyclicalReportModalContainer from './ConfirmCyclicalReport/ConfirmCyclicalReportModalContainer';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './ReportSidePaneComponent.css';

const baseClasses = getBEMClasses('reports-sidepane');

export default class ReportSidePane extends React.Component {
  static propTypes = {
    index: PropTypes.number
  };

  static defaultProps = {
    index: 0
  };

  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  componentDidMount() {
    this.props.onSidePaneOpen({
      hideHeader: true,
      customClass: 'reports-sidepane',
      classModifiers: 'with-tabs'
    });
  }

  getTabs = () => {
    return [
      {
        menuItem: this.context.t('Single Report'),
        render: () => <CreateReportFormContainer />
      },
      {
        menuItem: this.context.t('Cyclical Report'),
        render: () => <ScheduleReportComponent />
      }
    ];
  };

  render() {
    const { index } = this.props;

    return (
      <div className={baseClasses('body')}>
        <div className={baseClasses('header')}>
          {this.context.t('Create a Report')}
        </div>
        <Tab
          panes={this.getTabs()}
          customClass={`${baseClasses()}`}
          defaultActiveIndex={index}
        />
        <ConfirmSingleReportModalContainer />
        <ConfirmCyclicalReportModalContainer />
      </div>
    );
  }
}



