import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import APIcon from '../../common/APIcon/APIcon';
import './RetailFiatFormTabs.css';

const bemClasses = getBEMClasses('retail-fiat-tabs');

class RetailFiatTabs extends React.Component {
  render() {
    const { isSend, toggleTab } = this.props;
    return (
      <header className={bemClasses()}>
        <div
          className={bemClasses('tab', { selected: isSend })}
          onClick={() => !isSend && toggleTab()}>
          <APIcon name={'deposit'} customClass={bemClasses('tab-icon')} />
          {this.context.t('Deposit')}
        </div>
        <div
          className={bemClasses('tab', { selected: !isSend })}
          onClick={() => isSend && toggleTab()}>
          <APIcon name={'withdraw'} customClass={bemClasses('tab-icon')} />
          {this.context.t('Withdraw')}
        </div>
      </header>
    );
  }
}

RetailFiatTabs.propTypes = {
  isSend: PropTypes.bool.isRequired,
  toggleTab: PropTypes.func.isRequired
};

RetailFiatTabs.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailFiatTabs;



