import { validate, SUPPORTED_CURRENCIES } from 'coinslot';

const isSupportedCurrency = symbol => {
  return Object.values(SUPPORTED_CURRENCIES).includes(symbol);
};

export default (productSymbol, errorMessage) => address => {
  errorMessage = errorMessage || ' ';

  if (isSupportedCurrency(productSymbol)) {
    return validate(address, productSymbol) ? undefined : errorMessage;
  }
  return undefined;
};



