import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../../../common/Modal/Modal';
import APLabelWithText from '../../../common/APLabelWithText/APLabelWithText';
import APIcon from '../../../common/APIcon/APIcon';
import { formatDateTime } from '../../../../helpers/dateHelper';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import { templateConfirmRenderer } from '../../../../helpers/formTemplateHelper';

import './RetailFiatWithdrawModal.css';

const RetailFiatWithdrawModalClasses = getBEMClasses('fiat-withdraw-modal');

const RetailFiatWithdrawModal = (props, context) => {
  const { template, payload } = props;
  const { ProductSymbol, ProductFullName, Amount } = payload;

  return (
    <Modal
      isOpen={props.isOpen}
      title={
        <div className={RetailFiatWithdrawModalClasses('header-content')}>
          <APIcon
            name={'withdrawWithBorder'}
            customClass={RetailFiatWithdrawModalClasses('icon')}
          />
          <div className={RetailFiatWithdrawModalClasses('header-title')}>
            {`${context.t('Withdraw')} ${ProductSymbol} ${ProductFullName}`}
          </div>
        </div>
      }
      close={props.close}
      footer={{
        buttonText: context.t('Confirm'),
        buttonStyle: 'additive',
        onClick: () => props.submitWithdraw(props.payload)
      }}
      customClass={RetailFiatWithdrawModalClasses()}>
      <div className={RetailFiatWithdrawModalClasses('title')}>
        {context.t('Confirmation')}
      </div>
      <div className={RetailFiatWithdrawModalClasses()}>
        <APLabelWithText
          label={context.t('Amount to Withdraw')}
          text={`${Amount} ${ProductSymbol}`}
          customClass={RetailFiatWithdrawModalClasses()}
        />

        {templateConfirmRenderer(
          template,
          payload,
          RetailFiatWithdrawModalClasses,
          context
        )}

        <APLabelWithText
          label={context.t('Fee')}
          text={props.fee}
          customClass={RetailFiatWithdrawModalClasses()}
        />

        <APLabelWithText
          label={context.t('Time')}
          text={formatDateTime(new Date())}
          customClass={RetailFiatWithdrawModalClasses()}
        />
      </div>
    </Modal>
  );
};

RetailFiatWithdrawModal.propTypes = {
  payload: PropTypes.object,
  close: PropTypes.func.isRequired
};

RetailFiatWithdrawModal.defaultProps = {
  payload: {
    FullName: '',
    Amount: '',
    BankAccountName: '',
    BankAccountNumber: '',
    BankAddress: '',
    Comment: ''
  }
};

RetailFiatWithdrawModal.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailFiatWithdrawModal;



