import { connect } from 'react-redux';
import ReceiveComponent from './ReceiveComponent';
import { kycVerificationLevelsSelector } from './../../../redux/selectors/kycLevelsSelectors';
import { closeSidePane } from '../../../redux/actions/sidePaneActions';

const mapStateToProps = state => {
  const { disableDeposit } = kycVerificationLevelsSelector(state);
  return { disableDeposit };
};

const mapDispatchToProps = dispatch => ({
  verificationOnClick: () => dispatch(closeSidePane())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ReceiveComponent);



