import { createSelector } from 'reselect';

export const positionByProductIdSelector = productId =>
  createSelector(
    state => state.position.positions,
    positions => {
      if (productId) {
        return Object.keys(positions)
          .map(key => positions[key])
          .find(position => position.ProductId === productId);
      }

      return {};
    }
  );



