import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import {
  getFinalBalanceWithdrawValue,
  getAvailableBalance
} from '../../../helpers/withdrawHelper';
import RetailFiatDepositFormContainer from './RetailFiatDeposit/RetailFiatDepositFormContainer';
import RetailFiatWithdrawFormContainer from './RetailFiatWithdraw/RetailFiatWithdrawFormContainer';
import RetailFiatDepositModalContainer from './RetailFiatDeposit/RetailFiatDepositModalContainer';
import RetailFiatWithdrawModalContainer from './RetailFiatWithdraw/RetailFiatWithdrawModalContainer';
import RetailFiatSidePaneHeaderComponent from './RetailFiatSidePaneHeaderComponent';
import RetailFiatFormTabsComponent from './RetailFiatFormTabsComponent';
import RetailFiatDepositDetailsComponent from './RetailFiatDeposit/RetailFiatDepositDetailsComponent';
import RetailFiatWithdrawDetailsComponent from './RetailFiatWithdraw/RetailFiatWithdrawDetailsComponent';
import './RetailFiatSidePaneComponent.css';

const retailFiatSidePaneClasses = getBEMClasses('retail-fiat-sidepane');

class RetailFiatSidePane extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isSend: props.isSend,
      data: {},
      openConfirmModal: false
    };
  }

  componentDidMount() {
    this.props.onSidePaneOpen({
      customClass: this.props.showDetails
        ? 'retail-sidepane-with-details'
        : 'retail-sidepane',
      classModifiers: 'with-tabs',
      hideHeader: true
    });
  }

  onTabChange = () => {
    this.setState({
      isSend: !this.state.isSend,
      data: {}
    });
  };

  onUpdate = () => {
    this.getWithdrawFee();
    this.setTotal();
  };

  setTotal = () => {
    const { position, fee } = this.props;
    const {
      data: { Amount }
    } = this.state;
    const amount = parseFloat(Amount);
    const balance = getAvailableBalance(position.Amount, position.Hold);
    let total = 0;

    if (amount) {
      total = getFinalBalanceWithdrawValue(balance, amount, fee);
    }

    this.setState({ total });
  };

  getWithdrawFee = () => {
    const {
      product: { ProductId },
      getWithdrawFee
    } = this.props;
    const {
      data: { Amount }
    } = this.state;

    if (!isNaN(Amount)) {
      getWithdrawFee(ProductId, Amount);
    }
  };

  onDataUpdate = data => {
    const {
      data: { Amount },
      isSend
    } = this.state;
    this.setState({ data }, () => {
      if (!isSend && data.Amount !== Amount) {
        this.onUpdate();
      }
    });
  };

  renderInstructions = modificator => {
    return (
      <section
        className={retailFiatSidePaneClasses('side-details', modificator)}>
        <header
          className={retailFiatSidePaneClasses(
            'instructions-header',
            modificator
          )}>
          {this.context.t('Instructions')}
        </header>
        <section
          className={retailFiatSidePaneClasses(
            'address-instructions',
            modificator
          )}>
          <div>{this.context.t(`Step 1: Create the deposit ticket.`)}</div>
          <div>
            {this.context.t(
              `Step 2: Process deposit instructions on the deposit receipt.`
            )}
          </div>
        </section>
      </section>
    );
  };

  render() {
    const {
      product,
      position,
      fee,
      showDetails,
      productConfig,
      withdrawDetailsInfo
    } = this.props;

    const { isSend, data, total } = this.state;
    const balance = getAvailableBalance(position.Amount, position.Hold);

    return (
      <div className={retailFiatSidePaneClasses()}>
        <section className={retailFiatSidePaneClasses('main')}>
          <RetailFiatSidePaneHeaderComponent product={product} />
          <section className={retailFiatSidePaneClasses('main-form')}>
            <RetailFiatFormTabsComponent
              isSend={isSend}
              toggleTab={this.onTabChange}
              product={product}
              balance={balance}
            />
            {isSend ? (
              <RetailFiatDepositFormContainer
                product={product}
                onChange={this.onDataUpdate}
              />
            ) : (
              <RetailFiatWithdrawFormContainer
                product={product}
                balance={balance}
                fee={fee}
                total={total}
                onChange={this.onDataUpdate}
                showDetails={showDetails}
              />
            )}
          </section>
          {isSend && !showDetails
            ? this.renderInstructions('no-details')
            : null}
        </section>

        {showDetails && (
          <section className={retailFiatSidePaneClasses('side')}>
            {isSend ? (
              <RetailFiatDepositDetailsComponent
                info={productConfig.instructions}
              />
            ) : (
              <RetailFiatWithdrawDetailsComponent
                data={data}
                fee={fee}
                product={product}
                balance={balance}
                detailsInfo={withdrawDetailsInfo}
              />
            )}
          </section>
        )}

        <RetailFiatDepositModalContainer />
        <RetailFiatWithdrawModalContainer />
      </div>
    );
  }
}

RetailFiatSidePane.defaultProps = {
  showDetails: true
};

RetailFiatSidePane.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailFiatSidePane;



