import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import RetailFiatDepositFormComponent from './RetailFiatDepositFormComponent';
import {
  createDepositTicket,
  fetchProductDepositTemplate,
  fetchAllProductDepositTemplates
} from '../../../../redux/actions/depositActions';
import { openModal, MODAL_TYPES } from '../../../../redux/actions/modalActions';
import { kycVerificationLevelsSelector } from '../../../../redux/selectors/kycLevelsSelectors';
import { closeSidePane } from '../../../../redux/actions/sidePaneActions';

const mapStateToProps = state => {
  const { disableDeposit } = kycVerificationLevelsSelector(state);
  return {
    disableDeposit,
    depositInfo: state.deposit,
    products: state.product
  };
};

const mapDispatchToProps = dispatch => ({
  confirmDeposit: createDepositTicket,
  verificationOnClick: () => dispatch(closeSidePane()),
  selectDepositProduct: productId =>
    dispatch(fetchAllProductDepositTemplates(productId)),
  selectDepositTemplate: (productId, templateType) =>
    dispatch(fetchProductDepositTemplate(productId, templateType))
});

const RetailFiatDepositForm = reduxForm({
  form: 'createFiatDeposit',
  onSubmit: (payload, dispatch, { depositInfo: { template, templateInfo } }) =>
    dispatch(
      openModal(MODAL_TYPES.CONFIRM_FIAT_DEPOSIT, {
        payload,
        template,
        templateInfo
      })
    )
})(RetailFiatDepositFormComponent);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailFiatDepositForm);



