import { connect } from 'react-redux';
import KYCFormSidePaneComponent from './KYCFormSidePaneComponent';
import { kycLevelFormSelector } from '../../redux/selectors/kycLevelsSelectors';
import { getKYCFormComponent } from './KYCTypeSwitchHelper';

const mapStateToProps = state => {
  const { kycConfig } = kycLevelFormSelector(state);
  const FormComponent = getKYCFormComponent(kycConfig);

  return { FormComponent };
};

export default connect(mapStateToProps)(KYCFormSidePaneComponent);



