import { connect } from 'react-redux';
import { reduxForm, formValueSelector } from 'redux-form';
import CreateReportFormComponent from './CreateReportFormComponent';
import { openModal, MODAL_TYPES } from '../../../redux/actions/modalActions';
import { getReportsPayload } from '../../../helpers/reportsPayloadHelper';

const selector = formValueSelector('createReport');

const mapStateToProps = state => {
  const { accounts, selectedAccountId } = state.user;
  return {
    accounts,
    selectedAccountId,
    initialValues: {
      type: 'TradeActivity',
      [selectedAccountId]: true
    },
    startDate: selector(state, 'startDate')
  };
};

const CreateReportForm = reduxForm({
  form: 'createReport',
  onSubmit: (values, dispatch, ownProps) => {
    const payload = getReportsPayload(values, ownProps);
    return dispatch(openModal(MODAL_TYPES.CONFIRM_SINGLE_REPORT, { payload }));
  }
})(CreateReportFormComponent);

export default connect(mapStateToProps)(CreateReportForm);



