import React from 'react';
import PropTypes from 'prop-types';
import SidePaneDetailsComponent from '../../../common/SidePaneDetailsComponent/SidePaneDetailsComponent';
import { formatNumberToLocale } from '../../../../helpers/numberFormatter';

class RetailFiatWithdrawDetailsComponent extends React.Component {
  render() {
    const {
      data: { Amount },
      product: { ProductSymbol },
      balance,
      fee,
      detailsInfo
    } = this.props;

    const { getItem } = SidePaneDetailsComponent;
    const currentBalance = balance
      ? `${formatNumberToLocale(balance, ProductSymbol)} ${ProductSymbol}`
      : '-';
    const amount = Amount
      ? `${formatNumberToLocale(Amount, ProductSymbol)} ${ProductSymbol}`
      : '-';
    const remainingBalance =
      balance && Amount
        ? `${formatNumberToLocale(
            balance - Amount - fee,
            ProductSymbol
          )} ${ProductSymbol}`
        : '-';
    const items = [
      getItem(
        this.context.t(`Your current ${ProductSymbol} Balance`),
        currentBalance
      ),
      getItem(this.context.t('Amount to Withdraw'), amount),
      {
        label: this.context.t('Fee'),
        text: fee
          ? `${formatNumberToLocale(fee, ProductSymbol)} ${ProductSymbol}`
          : '-'
      },
      getItem(this.context.t('Remaining Balance'), remainingBalance)
    ];
    const info = detailsInfo.map(item => {
      if (item.useLink) {
        return (
          <a href={item.linkAddress}>
            {this.context.t('{text}', { text: item.text })}
          </a>
        );
      } else {
        return this.context.t('{text}', { text: item.text });
      }
    });

    return (
      <SidePaneDetailsComponent
        title={this.context.t('Balances')}
        items={items}
        info={info}
        classModifiers={'withdraw'}
      />
    );
  }
}

RetailFiatWithdrawDetailsComponent.propTypes = {
  FullName: PropTypes.string,
  Amount: PropTypes.number,
  BankAccountName: PropTypes.string,
  BankAccountNumber: PropTypes.string,
  BankAddress: PropTypes.string,
  Comment: PropTypes.string
};

RetailFiatWithdrawDetailsComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailFiatWithdrawDetailsComponent;



