import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import { showTemplateSelect } from '../../../../helpers/withdrawHelper';
import Spinner from '../../../common/Spinner/Spinner';
import VerificationRequiredContainer from '../../../VerificationRequired/VerificationRequiredContainer';
import DepositTemplateFormContainer from '../../../DepositSidePaneComponents/DepositTemplateFormContainer';
import DepositWorkflowsContainer from '../../../DepositSidePaneComponents/DepositWorkflows/DepositWorkflowsContainer';
import classnames from 'classnames';
import APSelect from '../../../../components/common/APSelect/APSelect';

import './RetailFiatDepositFormComponent.css';

const baseClasses = getBEMClasses('retail-deposit-form');
const slidePaneClasses = getBEMClasses('retail-slide-pane');

class RetailFiatDepositFormComponent extends React.Component {
  componentWillMount() {
    const {
      product: { ProductId },
      selectDepositProduct
    } = this.props;
    selectDepositProduct(ProductId);
  }

  componentDidMount() {
    this.props.initialize({ ...this.props.product });
  }

  render() {
    const {
      disableDeposit,
      depositInfo: deposit,
      depositInfo: { templateTypes, isLoading, error, depositInfo },
      product,
      handleSubmit,
      submitting,
      selectDepositTemplate,
      verificationOnClick
    } = this.props;
    const showTemplateTypeSelect = showTemplateSelect(deposit);
    const templateTypeOptions = [
      { value: '', label: 'Select an option' },
      ...templateTypes.map(template => ({
        value: template.TemplateName,
        label: template.TemplateName
      }))
    ];

    return (
      <form
        className={classnames(slidePaneClasses('body'), baseClasses('body'))}
        onSubmit={handleSubmit}>
        <div className={baseClasses('form-body')}>
          <div className={baseClasses('frame')}>
            {disableDeposit ? (
              <VerificationRequiredContainer
                disabled={disableDeposit}
                onClick={verificationOnClick}
              />
            ) : (
              <React.Fragment>
                {error && (
                  <p className={baseClasses('error')}>
                    {this.context.t(error)}
                  </p>
                )}

                {isLoading ? (
                  <Spinner customClass={baseClasses} />
                ) : (
                  <React.Fragment>
                    {showTemplateTypeSelect && (
                      <APSelect
                        name="TemplateType"
                        customClass={baseClasses()}
                        label={this.context.t(
                          'Select an option to continue the deposit process'
                        )}
                        onSelect={value =>
                          selectDepositTemplate(product.ProductId, value)
                        }
                        options={templateTypeOptions}
                      />
                    )}

                    {!depositInfo.length && (
                      <DepositTemplateFormContainer
                        submitting={submitting}
                        baseClasses={baseClasses}
                      />
                    )}

                    <DepositWorkflowsContainer
                      onSubmit={handleSubmit}
                      submitting={submitting}
                    />
                  </React.Fragment>
                )}
              </React.Fragment>
            )}
          </div>
        </div>
      </form>
    );
  }
}

RetailFiatDepositFormComponent.defaultProps = {
  deposit: {
    templateInfo: {},
    template: {},
    isLoading: false,
    error: '',
    depositInfo: {},
    depositStatus: {}
  }
};

RetailFiatDepositFormComponent.propTypes = {
  disableDeposit: PropTypes.bool,
  handleSubmit: PropTypes.func,
  submitting: PropTypes.bool,
  showSnack: PropTypes.func,
  deposit: PropTypes.shape({
    templateInfo: PropTypes.object.isRequired,
    template: PropTypes.object.isRequired,
    isLoading: PropTypes.bool.isRequired,
    error: PropTypes.string.isRequired,
    product: PropTypes.number,
    depositInfo: PropTypes.oneOfType([PropTypes.array, PropTypes.object])
      .isRequired,
    depositStatus: PropTypes.shape({
      success: PropTypes.bool,
      RequestCode: PropTypes.string
    }).isRequired
  }),
  verificationOnClick: PropTypes.func
};

RetailFiatDepositFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailFiatDepositFormComponent;



