import React from 'react';
import PropTypes from 'prop-types';
import { kycConfigLevelShape } from '../../propTypes/kycConfigShapePropTypes';

import './KYCFormSidePaneComponent.css';

export default class KYCFormSidePaneComponent extends React.Component {
  static propTypes = {
    closeSidePane: PropTypes.func.isRequired,
    kycConfig: PropTypes.shape({
      type: PropTypes.string,
      levels: PropTypes.arrayOf(PropTypes.shape(kycConfigLevelShape))
    })
  };

  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  componentDidMount() {
    this.props.onSidePaneOpen({
      title: this.context.t('Increase Verification Level'),
      customClass: 'kyc-form-sidepane',
      classModifiers: 'with-tabs'
    });
  }

  render() {
    const { FormComponent, ...props } = this.props;

    return <FormComponent {...props} />;
  }
}



