import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { sellValue } from '../../constants/sendOrder/orderEntryFormConstants';

const AdvancedOrderLimitPrice = (props, context) => {
  const { baseClass, symbol1, symbol2, value, formSide } = props;

  const bemClasses = getBEMClasses([baseClass]);
  const priceTitle =
    formSide === sellValue ? context.t('Bid Price') : context.t('Ask Price ');

  return (
    <div className={bemClasses('limit-price-block')}>
      <div className={bemClasses('limit-price-block-label')}>
        {`${priceTitle} ${symbol1} (${symbol2})`}
      </div>

      <div className={bemClasses('limit-price-block-value')}>{value}</div>
    </div>
  );
};

AdvancedOrderLimitPrice.propTypes = {
  baseClass: PropTypes.string.isRequired,
  symbol1: PropTypes.string.isRequired,
  symbol2: PropTypes.string.isRequired,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  formSide: PropTypes.string
};

AdvancedOrderLimitPrice.contextTypes = {
  t: PropTypes.func.isRequired
};

export default AdvancedOrderLimitPrice;



