import { connect } from 'react-redux';
import { get } from 'lodash';
import SendReceiveSidePaneComponent from '../../components/SendReceiveSidePane/SendReceiveSidePaneComponent';
import {
  createWithdrawTicket,
  getWithdrawFee
} from '../../redux/actions/withdrawActions';
import {
  requestTransferFunds,
  transferFunds
} from '../../redux/actions/transferActions';
import { positionByProductIdSelector } from '../../redux/selectors/sendReceiveSidePaneSelectors';
import config from '../../config';

const maptStateToProps = (state, ownProps) => {
  const productId = get(ownProps, 'product.ProductId', null);
  const productSymbol = get(ownProps, 'product.ProductSymbol', null);
  const position = positionByProductIdSelector(productId)(state);
  const decimalPlaces = state.product.decimalPlaces[productSymbol];
  const product = { ...ownProps.product, decimalPlaces };
  const { allowTransfer, hideFees } = config.global;

  const { withdrawFee: fee, template } = state.withdraw;

  return { position, fee, template, product, allowTransfer, hideFees };
};

const mapDispatchToProps = (dispatch, ownProps) => ({
  receiveFunds: payload =>
    dispatch(requestTransferFunds(payload, ownProps.name)),
  sendFunds: payload => dispatch(transferFunds(payload, ownProps.name)),
  sendToAddress: payload =>
    dispatch(createWithdrawTicket(payload, ownProps.name)),
  getWithdrawFee: (productId, amount) =>
    dispatch(getWithdrawFee(productId, amount))
});

export default connect(
  maptStateToProps,
  mapDispatchToProps
)(SendReceiveSidePaneComponent);



