export const getReportsPayload = (values, props) => {
  const accounts = props.accounts.map(a => a.AccountId);
  const accountIdList = accounts.filter(id => values[id]);
  const payload = { accountIdList };

  Object.keys(values)
    .filter(prop => !accounts.includes(prop))
    .forEach(prop => (payload[prop] = values[prop]));

  return payload;
};



