import { connect } from 'react-redux';
import ConfirmCyclicalReportModal from './ConfirmCyclicalReportModal';
import modalDecorator from '../../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../../redux/actions/modalActions';
import { scheduleReport } from '../../../redux/actions/reportActions';

const mapDispatchToProps = {
  scheduleReport
};

const container = connect(
  null,
  mapDispatchToProps
)(ConfirmCyclicalReportModal);

export default modalDecorator({
  name: MODAL_TYPES.CONFIRM_CYCLICAL_REPORT
})(container);



