import React from 'react';
import PropTypes from 'prop-types';
import WithdrawFormContainer from './WithdrawForm/WithdrawFormContainer';

class WithdrawSidePaneComponent extends React.Component {
  componentDidMount() {
    this.props.onSidePaneOpen &&
      this.props.onSidePaneOpen({
        title: 'Withdrawals',
        customClass: 'withdraw-sidepane'
      });
  }

  render() {
    return <WithdrawFormContainer {...this.props} />;
  }
}

WithdrawSidePaneComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default WithdrawSidePaneComponent;



