import React from 'react';
import PropTypes from 'prop-types';
import DepositFormContainer from './DepositFormContainer';

class DepositSidePaneComponent extends React.Component {
  componentDidMount() {
    this.props.onSidePaneOpen({
      title: this.context.t('Deposit'),
      customClass: 'deposit-sidepane'
    });
  }

  render() {
    return <DepositFormContainer {...this.props} />;
  }
}

DepositSidePaneComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default DepositSidePaneComponent;



