import React from 'react';
import PropTypes from 'prop-types';
import APInput from '../APInput/APInput';
import cryptoAddressValidator from '../../../helpers/cryptoAddressValidationHelper/cryptoAddressValidationHelper';

const APCryptoAddressInputComponent = (props, context) => {
  const { productSymbol, ...otherProps } = props;
  const errorMessage = context.t('Invalid {productSymbol} address', {
    productSymbol
  });

  const validator = cryptoAddressValidator(productSymbol, errorMessage);
  return <APInput {...otherProps} validate={validator} />;
};

APCryptoAddressInputComponent.propTypes = {
  productSymbol: PropTypes.string.isRequired,
  ...APInput.propTypes
};

APCryptoAddressInputComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default APCryptoAddressInputComponent;



