import { connect } from 'react-redux';
import NewApiKeyComponent from './NewApiKeyModal';
import { addApiKey, removeApiKey } from '../../../redux/actions/apiKeyActions';
import modalDecorator from '../../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../../redux/actions/modalActions';
import { closeApiKeysSidePane } from '../../../redux/actions/sidePaneActions';

const mapStateToProps = state => ({
  numberOfApiKeys: state.apiKey.apiKeys.length,
  userId: state.user.userInfo.UserId
});

const mapDispatchToProps = dispatch => ({
  addApiKey: apiKey => {
    dispatch(addApiKey(apiKey));
    dispatch(closeApiKeysSidePane());
  },
  removeApiKey: apiKey => dispatch(removeApiKey(apiKey))
});

const container = connect(
  mapStateToProps,
  mapDispatchToProps
)(NewApiKeyComponent);

export default modalDecorator({
  name: MODAL_TYPES.NEW_API_KEY
})(container);



