import React from 'react';
import { connect } from 'react-redux';
import { reduxForm, formValueSelector } from 'redux-form';
import VerificationRequiredContainer from '../../VerificationRequired/VerificationRequiredContainer';
import { productsListSelector } from '../../../redux/selectors/depositWithdrawalSelectors';
import { positionSelector } from '../../../redux/selectors/positionSelectors';
import { kycVerificationLevelsSelector } from '../../../redux/selectors/kycLevelsSelectors';
import WithdrawFormComponent from './WithdrawFormComponent';
import {
  getWithdrawFee,
  fetchProductWithdrawTemplates,
  createWithdrawTicket,
  fetchProductWithdrawTemplate
} from '../../../redux/actions/withdrawActions';
import store from '../../../redux/store';
import { closeWithdrawSidePane } from '../../../redux/actions/sidePaneActions';
import config from '../../../config';

const formSelector = formValueSelector('createWithdraw');
const closeSidePane = () => store.dispatch(closeWithdrawSidePane());

const mapStateToProps = state => {
  const products = productsListSelector(state);
  const positions = positionSelector(state);
  const { disableWithdraw } = kycVerificationLevelsSelector(state);

  return {
    disableWithdraw,
    products,
    withdrawInfo: state.withdraw,
    positions,
    amountValue: formSelector(state, 'Amount'),
    selectedProductId: formSelector(state, 'ProductId'),
    hideFees: !!config.global.hideFees,
    VerificationRequiredComponent: (
      <VerificationRequiredContainer
        disabled={disableWithdraw}
        onClick={closeSidePane}
      />
    )
  };
};

const mapDispatchToProps = dispatch => ({
  selectWithdrawProduct: productId =>
    dispatch(fetchProductWithdrawTemplates(productId)),
  selectWithdrawTemplate: (productId, templateType) =>
    dispatch(fetchProductWithdrawTemplate(productId, templateType)),
  getWithdrawFee: (productId, amount) =>
    dispatch(getWithdrawFee(productId, amount)),
  submitWithdraw: payload => dispatch(createWithdrawTicket(payload))
});

const WithdrawFormContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(WithdrawFormComponent);

export default reduxForm({
  form: 'createWithdraw',
  onSubmit: () => {}
})(WithdrawFormContainer);



