import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import { templateFormRenderer } from '../../../../helpers/formTemplateHelper';
import { required, biggerThanZero } from '../../../../helpers/formValidations';
import { showTemplateSelect } from '../../../../helpers/withdrawHelper';
import WithdrawWorkflowsContainer from '../../../WithdrawSidePaneComponents/WithdrawWorkflows/WithdrawWorkflowsContainer';
import VerificationRequiredContainer from '../../../VerificationRequired/VerificationRequiredContainer';
import Spinner from '../../../common/Spinner/Spinner';
import APButton from '../../../common/APButton';
import APLabelWithText from '../../../common/APLabelWithText/APLabelWithText';
import APNumberInput from '../../../common/APNumberInput';
import APSelect from '../../../common/APSelect/APSelect';
import classnames from 'classnames';

import './RetailFiatWithdrawFormComponent.css';

const baseClasses = getBEMClasses('retail-withdraw-form');
const slidePaneClasses = getBEMClasses('retail-slide-pane');

class RetailFiatWithdrawFormComponent extends React.Component {
  componentWillMount() {
    const { product, selectWithdrawProduct } = this.props;
    selectWithdrawProduct(product.ProductId);
  }

  componentDidMount() {
    this.props.initialize({ ...this.props.product });
  }

  render() {
    const {
      handleSubmit,
      disableWithdraw,
      withdrawInfo,
      withdrawInfo: {
        withdrawWorkflowInfo,
        template,
        templateTypes,
        isLoading,
        error
      },
      product,
      product: { ProductSymbol, ProductId },
      balance,
      fee,
      total,
      showDetails,
      verificationOnClick,
      selectWithdrawTemplate
    } = this.props;
    const showTemplateTypeSelect = showTemplateSelect(withdrawInfo);
    const useExternalWithdrawWorkflow =
      withdrawWorkflowInfo && Object.values(withdrawWorkflowInfo).length;
    const templateTypeOptions = [
      { value: '', label: 'Select an option' },
      ...templateTypes.map(template => ({
        value: template.TemplateName,
        label: template.TemplateName
      }))
    ];
    const { DecimalPlaces } = this.props.products;

    return (
      <form
        className={classnames(slidePaneClasses('body'), baseClasses('body'))}
        onSubmit={handleSubmit}>
        <div className={baseClasses('form-body')}>
          <div className={baseClasses('frame')}>
            {disableWithdraw ? (
              <VerificationRequiredContainer
                disabled={disableWithdraw}
                onClick={verificationOnClick}
              />
            ) : (
              <React.Fragment>
                {showTemplateTypeSelect && (
                  <APSelect
                    name="TemplateType"
                    customClass={baseClasses()}
                    label={this.context.t(
                      'Select an option to continue the withdraw process'
                    )}
                    onSelect={value => selectWithdrawTemplate(ProductId, value)}
                    options={templateTypeOptions}
                  />
                )}

                {!showTemplateTypeSelect &&
                  !template.hasOwnProperty('Amount') && (
                    <APNumberInput
                      type="text"
                      name="Amount"
                      labelInInput={ProductSymbol}
                      label={this.context.t(
                        `Amount of ${ProductSymbol} to Withdraw`
                      )}
                      decimalPlaces={DecimalPlaces}
                      customClass={baseClasses()}
                      validate={[required, biggerThanZero]}
                    />
                  )}

                {!useExternalWithdrawWorkflow &&
                  templateFormRenderer(
                    template,
                    baseClasses(''),
                    this.context,
                    product
                  )}

                <WithdrawWorkflowsContainer />

                {!showDetails ? (
                  <div className={baseClasses('info-items')}>
                    <APLabelWithText
                      label={this.context.t('Current Balance:')}
                      text={`${balance} ${ProductSymbol}`}
                      customClass={baseClasses()}
                    />
                    <APLabelWithText
                      label={this.context.t('Transaction Fee:')}
                      text={fee !== undefined ? fee : '-'}
                      customClass={baseClasses()}
                    />
                    <APLabelWithText
                      label={this.context.t('Final Balance:')}
                      text={total !== undefined ? total : '-'}
                      customClass={baseClasses()}
                    />
                  </div>
                ) : null}

                {error && (
                  <p className={baseClasses('error')}>
                    {this.context.t(error)}
                  </p>
                )}

                {isLoading && <Spinner customClass={baseClasses} />}
              </React.Fragment>
            )}
          </div>
          {!showTemplateTypeSelect &&
            !disableWithdraw &&
            !useExternalWithdrawWorkflow && (
              <div className={baseClasses('footer')}>
                <APButton
                  customClass={baseClasses()}
                  disabled={disableWithdraw}
                  type="submit"
                  styleName="additive">
                  {this.context.t('Withdraw')} {ProductSymbol}
                </APButton>
              </div>
            )}
        </div>
      </form>
    );
  }
}

RetailFiatWithdrawFormComponent.defaultProps = {
  withdrawInfo: {
    template: {},
    isLoading: false,
    error: '',
    withdrawInfo: {},
    withdrawStatus: {}
  }
};

RetailFiatWithdrawFormComponent.propTypes = {
  handleSubmit: PropTypes.func,
  submitting: PropTypes.bool,
  showSnack: PropTypes.func,
  withdrawInfo: PropTypes.shape({
    template: PropTypes.object.isRequired,
    isLoading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    product: PropTypes.number,
    withdrawInfo: PropTypes.oneOfType([PropTypes.array, PropTypes.object])
      .isRequired,
    withdrawStatus: PropTypes.shape({
      success: PropTypes.bool,
      RequestCode: PropTypes.string
    }).isRequired
  }),
  disableWithdraw: PropTypes.bool,
  verificationOnClick: PropTypes.func
};

RetailFiatWithdrawFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailFiatWithdrawFormComponent;



