import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../../../common/Modal/Modal';
import APLabelWithText from '../../../common/APLabelWithText/APLabelWithText';
import APIcon from '../../../common/APIcon/APIcon';
import { formatDateTime } from '../../../../helpers/dateHelper';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import { templateConfirmRenderer } from '../../../../helpers/formTemplateHelper';

import './RetailFiatDepositModal.css';

const RetailFiatDepositModalClasses = getBEMClasses('fiat-deposit-modal');

const shouldRenderBankInfo = bankInfo => {
  return bankInfo.name && bankInfo.wireNumber && bankInfo.accountNumber;
};

const RetailFiatDepositModal = (props, context) => {
  const {
    payload: { ProductSymbol, fullname, amount, comments, GivenName, Amount },
    template,
    templateInfo = {},
    payload
  } = props;

  const templateFields = templateConfirmRenderer(
    template,
    payload,
    RetailFiatDepositModalClasses,
    context
  );

  return (
    <Modal
      isOpen={props.isOpen}
      title={
        <div className={RetailFiatDepositModalClasses('header-content')}>
          <APIcon
            name={'depositWithBorder'}
            size={44}
            customClass={RetailFiatDepositModalClasses('icon')}
          />
          <div className={RetailFiatDepositModalClasses('header-title')}>
            {`${context.t('Deposit')} ${ProductSymbol}`}
          </div>
        </div>
      }
      close={props.close}
      footer={{
        buttonText: context.t('Confirm'),
        buttonStyle: 'additive',
        onClick: () => {
          props.submitDeposit(payload);

          if (props.showDepositIdModal) {
            props.openReferenceModal();
          }
        }
      }}
      customClass={RetailFiatDepositModalClasses()}>
      <div className={RetailFiatDepositModalClasses('title')}>
        {context.t('Confirmation')}
      </div>
      <div className={RetailFiatDepositModalClasses()}>
        {templateFields.length > 0 ? (
          templateFields
        ) : (
          <React.Fragment>
            <APLabelWithText
              label={context.t('Full Name')}
              text={fullname || GivenName}
              customClass={RetailFiatDepositModalClasses()}
            />
            <APLabelWithText
              label={context.t('Amount to Deposit')}
              text={amount || Amount}
              customClass={RetailFiatDepositModalClasses()}
            />
            <APLabelWithText
              label={context.t('Note')}
              text={comments || '-'}
              customClass={RetailFiatDepositModalClasses()}
            />
            <APLabelWithText
              label={context.t('Time')}
              text={formatDateTime(new Date())}
              customClass={RetailFiatDepositModalClasses()}
            />
          </React.Fragment>
        )}

        {templateInfo.DepositWorkflow === 'ManualDeposit' &&
          shouldRenderBankInfo(props.bankInfo) && (
            <React.Fragment>
              <div className={RetailFiatDepositModalClasses('bank-info')}>
                <APLabelWithText
                  label={context.t('Step 2: Wire the funds to this account')}
                  customClass={RetailFiatDepositModalClasses()}
                  classModifiers="bank-info"
                />
                <APLabelWithText
                  label={context.t('Bank Name:')}
                  text={props.bankInfo.name}
                  customClass={RetailFiatDepositModalClasses()}
                  classModifiers="bank-info"
                />
                <APLabelWithText
                  label={context.t('Wire Number:')}
                  text={props.bankInfo.wireNumber}
                  customClass={RetailFiatDepositModalClasses()}
                  classModifiers="bank-info"
                />
                <APLabelWithText
                  label={context.t('Account Number:')}
                  text={props.bankInfo.accountNumber}
                  customClass={RetailFiatDepositModalClasses()}
                  classModifiers="bank-info"
                />
              </div>
              <div className={RetailFiatDepositModalClasses('information')}>
                <div
                  className={RetailFiatDepositModalClasses('information-text')}>
                  {context.t(
                    'Make sure the wire transfer matches the Name and Amount stated in this Deposit Ticket.'
                  )}
                </div>
                <div
                  className={RetailFiatDepositModalClasses('information-text')}>
                  {context.t(
                    'A copy of this Deposit Ticket will be emailed to you'
                  )}
                </div>
              </div>
            </React.Fragment>
          )}
      </div>
    </Modal>
  );
};

RetailFiatDepositModal.propTypes = {
  payload: PropTypes.object,
  close: PropTypes.func.isRequired
};

RetailFiatDepositModal.defaultProps = {
  payload: {
    fullname: '',
    amount: '',
    comments: ''
  }
};

RetailFiatDepositModal.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailFiatDepositModal;



